<template>
  <div class="home">
    <div class="md-layout md-alignment-top-center">
      <!-- <div class="md-layout-item"></div>
      <div class="md-layout-item"></div>-->
      <div class="md-layout-item md-size-70 md-small-size-100" style="background-color: white;">
        <h1 class="md-display-2">Трекер Covid-19 для України</h1>
        <h2 class="md-display-1">
          Всього інфіковано з 3 березня:
          <span style="color:red">{{totalConfirmed.total}}</span>
        </h2>

        <div class="md-layout-item md-layout md-gutter md-alignment-top-center text-stats">
          <div class="md-layout-item">
            <p class="md-subheading">
              На данний момент інфіковано:
              <span
                style="color:red"
              >{{totalConfirmed.total - totalRecovered.total - totalDeaths.total}}</span>
            </p>
          </div>

          <div class="md-layout-item">
            <p class="md-subheading">
              Видужало
              <span style="color:green">{{totalRecovered.total}}</span>
            </p>
          </div>

          <div class="md-layout-item">
            <p class="md-subheading">
              Загибло
              <span style="color: black">{{totalDeaths.total}}</span>
            </p>
          </div>
        </div>
        <div class="md-layout-item">
          <p>Останне оновлення {{totalConfirmed.totalConfirmedArrayDate[totalConfirmed.totalConfirmedArrayDate.length-1]}}</p>
        </div>
        <div class="chart-container">
          <canvas id="myChart" width="400" height="500"></canvas>
        </div>
        <div class="chart-container">
          <canvas id="recoveredChart" width="400" height="500"></canvas>
        </div>
        <div class="chart-container">
          <canvas id="deathsChart" width="400" height="500"></canvas>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
/* eslint-disable no-console */
import axios from "axios";
import Chart from "chart.js";

export default {
  name: "Home",
  components: {},
  data() {
    return {
      totalConfirmed: {
        total: 0,
        totalConfirmedArray: [],
        totalConfirmedArrayDate: []
      },

      totalRecovered: {
        total: 0,
        totalRecoveredArray: [],
        totalRecoveredArrayDate: []
      },
      totalDeaths: {
        total: 0,
        totalDeathsArray: [],
        totalDeathsArrayDate: []
      }
    };
  },

  mounted() {
    var ctx = document.getElementById("myChart").getContext("2d");
    var ctxR = document.getElementById("recoveredChart").getContext("2d");
    var ctxD = document.getElementById("deathsChart").getContext("2d");


    var confirmedChart = new Chart(ctx, {
      type: "line",
      data: {
        // labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
        labels: this.totalConfirmed.totalConfirmedArrayDate,

        datasets: [
          {
            label: "# Графік інфікування",
            data: this.totalConfirmed.totalConfirmedArray,
            backgroundColor: ["rgba(255, 99, 132, 0.2)"],

            borderColor: ["rgba(255, 99, 132, 1)"],

            pointBackgroundColor: "rgba(255, 0, 0, 1)",

            borderWidth: 2,
            fill: true
          }
        ]
      },
      options: {
        scales: {
          tooltips: {
            mode: "dataset"
          },
          yAxes: [
            {
              ticks: {
                beginAtZero: true
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                beginAtZero: true,
                max: this.totalConfirmed.total,
                min: 0
              }
            }
          ]
        },
        responsive: true,
        maintainAspectRatio: false
      }
    });

    var recoveredChart = new Chart(ctxR, {
      type: "line",
      data: {
        // labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
        labels: this.totalRecovered.totalRecoveredArrayDate,

        datasets: [
          {
            label: "# Графік видужання",
            data: this.totalRecovered.totalRecoveredArray,
            backgroundColor: ["rgba(31, 255, 68, 0.2)"],

            borderColor: ["rgba(0, 212, 35, 1)"],

            pointBackgroundColor: "rgba(0, 120, 20, 1)",

            borderWidth: 2,
            fill: true
          }
        ]
      },
      options: {
        scales: {
          tooltips: {
            mode: "dataset"
          },
          yAxes: [
            {
              ticks: {
                beginAtZero: true
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                beginAtZero: true,
                max: this.totalRecovered.total,
                min: 0
              }
            }
          ]
        },
        responsive: true,
        maintainAspectRatio: false
      }
    });

    var deathsChart = new Chart(ctxD, {
      type: "line",
      data: {
        // labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
        labels: this.totalDeaths.totalDeathsArrayDate,

        datasets: [
          {
            label: "# Графік смертей",
            data: this.totalDeaths.totalDeathsArray,
            backgroundColor: ["rgba(0, 0, 0, 0.4)"],

            borderColor: ["rgba(rgba(0, 0, 0, 1))"],

            pointBackgroundColor: "rgba(rgba(0, 0, 0, 1))",

            borderWidth: 2,
            fill: true
          }
        ]
      },
      options: {
        scales: {
          tooltips: {
            mode: "dataset"
          },
          yAxes: [
            {
              ticks: {
                beginAtZero: true
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                beginAtZero: true,
                max: this.totalRecovered.total,
                min: 0
              }
            }
          ]
        },
        responsive: true,
        maintainAspectRatio: false
      }
    });
  // Axios start
    axios
      .get("https://api.covid19api.com/country/ukraine/status/confirmed/live")
      .then(response => {
        console.log(response.data[response.data.length - 1].Cases);
        this.totalConfirmed.total =
          response.data[response.data.length - 1].Cases;
        response.data.forEach(element => {
          this.totalConfirmed.totalConfirmedArray.push(element.Cases);
          // this.totalConfirmed.totalConfirmedArrayDate.push(element.Date.toString())
          let day = new Date(element.Date).getDate();
          let month = new Date(element.Date).getMonth();
          let year = new Date(element.Date).getFullYear();
          this.totalConfirmed.totalConfirmedArrayDate.push(
            `${day}/${month + 1}/${year}`
          );
          console.log(new Date(element.Date));
          
        });
        this.totalConfirmed.totalConfirmedArray.splice(this.totalConfirmed.totalConfirmedArray-1, 39)
        this.totalConfirmed.totalConfirmedArrayDate.splice(this.totalConfirmed.totalConfirmedArrayDate-1, 39)


        confirmedChart.update();
        console.log(this.totalConfirmedArray);
      });
    // recovered
    axios
      .get("https://api.covid19api.com/country/ukraine/status/recovered/live")
      .then(response => {
        console.log(response.data[response.data.length - 1].Cases);
        this.totalRecovered.total = response.data[response.data.length - 1].Cases;

        response.data.forEach(element => {
          this.totalRecovered.totalRecoveredArray.push(element.Cases)
          let day = new Date(element.Date).getDate();
          let month = new Date(element.Date).getMonth();
          let year = new Date(element.Date).getFullYear();
          this.totalRecovered.totalRecoveredArrayDate.push(`${day}/${month + 1}/${year}`)
        })
        this.totalRecovered.totalRecoveredArray.splice(this.totalRecovered.totalRecoveredArray-1, 58)
        this.totalRecovered.totalRecoveredArrayDate.splice(this.totalRecovered.totalRecoveredArrayDate-1, 58)

        // this.totalRecovered.totalRecoveredArray = this.totalRecovered.totalRecoveredArray.slice(60)
        // this.totalRecovered.totalRecoveredArrayDate = this.totalRecovered.totalRecoveredArrayDate.slice(60)

        recoveredChart.update();
      });
    // deaths
    axios
      .get("https://api.covid19api.com/country/ukraine/status/deaths/live")
      .then(response => {
        console.log(response.data[response.data.length - 1].Cases);
        this.totalDeaths.total = response.data[response.data.length - 1].Cases;

        response.data.forEach(element => {
          this.totalDeaths.totalDeathsArray.push(element.Cases)
          let day = new Date(element.Date).getDate();
          let month = new Date(element.Date).getMonth();
          let year = new Date(element.Date).getFullYear();
          this.totalDeaths.totalDeathsArrayDate.push(`${day}/${month + 1}/${year}`)
        })
        this.totalDeaths.totalDeathsArray.splice(this.totalDeaths.totalDeathsArray.splice-1, 49)
        this.totalDeaths.totalDeathsArrayDate.splice(this.totalDeaths.totalDeathsArrayDate-1, 49)

        deathsChart.update()
      });
    // Axios end
  }
};
</script>

